.Expense{
    display: flex;
    justify-content :space-between;
    align-items: center;
    box-shadow: 0px 3px 8px rgb(5, 3, 54);
    background-color: rgb(193, 134, 248);
    margin-bottom: 1rem;
    border-radius: 10px;
    padding: 5px;
    font-size: 1.3rem;   
}
.Expense_date{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    border: 1px dashed rgb(6, 0, 0);
    border-radius: 10px;
    background-color: rgb(123, 121, 121);
    padding: 5px;
}
.Expense_title{
    display: flex;
    margin-left: 2rem;
    flex : 1;
    font-weight : bold;
}
.Expense_amount{
    display: flex;
    align-items: center;
    background-color: rgb(104, 107, 107);
    padding: 10px;
    border-radius: 10px;
}