.ExpenseFilter_control{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: rgb(85, 79, 79);
    border-radius: 8px;
    height: 3.5rem;
    margin-bottom: 1rem;
}
.ExpenseFilter_control label{
    font-size: 1.5rem;
}

.ExpenseFilter_control select{
    height: 2.5rem;
    width: 4rem;
    border-radius: 10px;
}