.chartBar_main{
    height : 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.chart_Bar{
    display: flex;
    height : 100%;
    width : 85%;
    border: 1px solid rgb(71, 68, 68);
    background-color: rgb(226, 59, 226);
    overflow: hidden;
    border-radius: 12px;
    flex-direction: column;
    justify-content: flex-end;
}
.chart_Bar_fill{
    background-color: blueviolet;
    width : 100%;
    transition :all 1s ease-out;
}
.Label{
    margin-bottom: 0.2rem;
    text-align: center;
}